<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-top">
          <div class="t-left">
            <ctitle :title="'信访量初件重件分布'"></ctitle>
            <div class="detail">
              <allCount class="all-count" :count="allcount"></allCount>
              <detailList :params="detailList"></detailList>
            </div>
          </div>
          <div class="t-center">
            <ctitle :title="'信访量地区分布'"></ctitle>
            <div class="inner">
              <!-- <div
                  v-if="barBackStatus"
                  class="map-back"
                  @click="barBack"
                ></div> -->
              <barline :params="qvfbBarParams"></barline>
            </div>
          </div>
          <div class="t-right">
            <ctitle :title="'信访量信访次数分布'"></ctitle>
            <div class="charts">
              <barline :params="xfcsBarParams"></barline>
            </div>
          </div>
      </div>
      <div class="c-bottom">
        <div class="menu">
          <div class="curitem" @click="menuShow=true">{{menuName}}</div>
          <ul class="menu-list clearfloat" v-if="menuShow">
            <li
              class="item"
              :class="menuIndex===index?'active':''"
              v-for="(item,index) of menuArr"
              :key="index"
              @click="menuChange(index)"
            >
              {{item}}
            </li>
            <b class="sj-l"></b>
            <b class="sj-r"></b>
            <b class="sj-t"></b>
            <b class="sj-b"></b>
          </ul>
        </div>
        <div class="b-left">
          <ctitle :title="'地区信访人群年龄分布'"></ctitle>
          <div class="charts">
            <barline :params="rqnlBarParams"></barline>
          </div>
        </div>
        <div class="b-center">
          <div class="w-charts">
            <ctitle :title="'地区信访重信访目的分布'"></ctitle>
            <div class="charts">
              <pie-chart3D :params="xfmdPieParams"></pie-chart3D>
            </div>
          </div>
          <div class="w-charts">
            <ctitle :title="'地区信访量投诉问题分布'"></ctitle>
            <div class="charts">
              <loopPie :params="tswtPieParams"></loopPie>
            </div>
          </div>
        </div>
        <div class="b-right">
          <ctitle :title="'地区信访量信访次数分布'"></ctitle>
          <div class="charts">
            <barline :params="dqxfcsBarParams"></barline>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import typeChange from "@/components/typeChange/index";
  import incNumber from "@/components/incNumber/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import loopPie from "@/components/pie/LoopPie";
  import barline from "@/components/bar/BarLine";
  // import barlineY from "@/components/bar/barLine2";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";

  export default {
    name: "cjcj",
    components: {
      ctitle,
      typeChange,
      incNumber,
      PieChart3D,
      loopPie,
      barline,
      allCount,
      detailList,
      // barlineY
    },
    data() {
      return {
        barBackStatus: false,
        loadFlag: false,
        curTypeId:'drop',
        // 各市切换列表
        menuShow: false,
        menuIndex: 0,
        menuName: "昆明市",
        menuArr: ["昆明市", "玉溪市", "曲靖市", "保山市", "昭通市", "楚雄市", "临沧市", "普洱市", "丽江市", "红河州", "文山州", "西双版纳", "大理州", "德宏州", "怒江州", "迪庆州"],

        // 信访量初信重信占比
        allcount: -1,
        detailList: null,
        // 信访量地区分布
        qvfbBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: false,
            barLabelShow: true,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: "13%", // label字号
            left: "5%",
            right: "5%",
            top: "0%",
            bottom: "0%",
            barLabelOptions: "top",
            barWidth: "25%", // 每个柱形图宽
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "数量"
            }
          }
        },
        // 信访量信访次数分布
        xfcsBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: false,
            splitLineShow: false, //value轴splitline是否显示
            axisLabelSize: "12%", // label字号
            axis1Name:"件次",
            left: "10%",
            right: "15%",
            top: "5%",
            bottom: "10%",
            barWidth: 30,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            itemLabelColor: "#cdefff",
            colors: {
              bar: [["#d2f69d", "#79cc7a"]]
            },
            map: {
              value: "次数"
            }
          }
        },
        // 地区信访人群年龄分布
        rqnlBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            yAxisColor: '#cdefff',
            xName: "name", //x轴字段
            axisValueShow: true,
            axis1Name:"",
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "0%",
            top: "5%",
            bottom: "10%",
            barLabelOptions: "top",
            barWidth: 30,
            splitLineShow: false,
            barLabelShow: true, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "#cdefff",
            colors: {
              bar: [["#ffa8c5", "#5850ff"]]
            },
            map: {
              value: "人数"
            }
          }
        },
        // 地区信访重信访目的分布
        xfmdPieParams: {
          data: [],
          angle: 35,
          radius: "40%",
          labelRadius: 25,
          depth3D: 25,
          fontSize: this.fontSize(0.16),
          // color:  this.$STANDARD.pieColors,
          color:  ['#d93e75', '#ffc833', '#4eed99', '#13f0ff', '#1ba1ff'],
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 地区信访量投诉问题分布
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: ['#4eed99', '#d93e75', '#ccbe51', '#eb9002', '#1ba1ff', '#563ad2', '#f89e67', '#13f0ff'],
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 0,
            borderColor: "#0B2244",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            threeLevel:true,
            rollFlag: true
          }
        },
        // 地区信访量信访次数分布
        dqxfcsBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            xName: "name", //x轴字段
            axisValueShow: false,
            splitLineShow: false, //value轴splitline是否显示
            axis1Name:"件次",
            axisLabelSize: "12%", // label字号
            left: "16%",
            right: "5%",
            top: "5%",
            bottom: "10%",
            barWidth: 30,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            itemLabelColor: "#cdefff",
            colors: {
              bar: [["#d2f69d", "#79cc7a"]]
            },
            map: {
              value: "次数"
            }
          }
        },
      };
    },
    mounted() {
      this.getAPI(this.year, this.curTypeId);
    },
    methods: {
      // canvasClick(item) {
      //   this.barBackStatus = true;
      //   this.curCity = item.params.name;
      //   let obj={color:'#fefa82'}
      //   // let c = ''
      //   // for(let key in this.mapJson) {
      //   //   if (key === item.params.name) {
      //   //     c = key;
      //   //   }
      //   // }
      //   // if (!c) {
      //     this.qvfbBarParams.data.forEach((item,index)=>{
      //       if(this.curCity == item.name){
      //         this.$set(item, "itemStyle", obj)
      //       }else{
      //         this.$delete(item,"itemStyle");
      //       }
      //     })
      //   // }
      // },
      // 地图返回全省
      // barBack() {
      //   this.barBackStatus = false;
      //   this.curCity = "广东省";
      // },
      // 各市列表切换
      menuChange(index) {
        this.menuIndex = index;
        this.menuName = this.menuArr[this.menuIndex];
        this.menuShow = false;
        this.getBtmAPI(this.year, this.curTypeId, this.menuName);
      },
      getAPI(year, id) {
        // 1.信访量初件重件分布（总数）
        get_http("xftstjfxCjcjChujXflcjcjfbTotal", {year: year})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        
        // 2.信访量初件重件分布(列表)
        get_http("xftstjfxCjcjChujXflcjcjfb", {year: year})
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });

        // 3.信访量地区分布
        get_http("xftstjfxCjcjChujXfldqfb", {
          // day: `[${date.startDate},${date.endDate}]`,
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xj_02,
          // name: this.nameP,
        })
        .then(res => {
          this.qvfbBarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        // 4.信访量信访次数分布
        get_http("xftstjfxCjcjChujXfcsfb", {year: year})
        .then(res => {
          this.xfcsBarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        
        this.getBtmAPI(year, this.menuName);
      },
      getBtmAPI(year, city) {
        // 5.地区信访人群年龄分布
        get_http("xftstjfxCjcjChujDqxfrnlfb", {year: year, shij_02: city})
          .then(res => {
            this.rqnlBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 6.地区信访重信访目的分布
        get_http("xftstjfxCjcjChujDqmdfb", {year: year, shij_02: city})
          .then(res => {
            this.xfmdPieParams.data =res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 7.地区信访量投诉问题分布
        get_http("xftstjfxCjcjChujDqtswtfb", {year: year, shij_02: city})
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 8.地区信访量信访次数分布
        get_http("xftstjfxCjcjChujDqxfcsfb", {year: year, shij_02: city})
          .then(res => {
            this.dqxfcsBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val, this.curTypeId);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .w-popup
      position fixed
      top 0
      left 0
      right 0
      bottom 0
      background rgba(0, 0, 0, 0.4)
      z-index 1005
      font-size 0

      .c-pop
        position absolute
        top 50%
        left 50%
        transform translate(-50%, -50%)
        width 12.7rem

        .search-box
          position absolute
          width 4.3rem
          height 0.5rem
          line-height 0.5rem
          top -0.5rem
          left 0
          // background #293974
          background #0d85be
          padding-left 0.2rem
          border-radius .05rem .05rem 0 0

          input
            width 3.5rem
            box-sizing border-box
            padding 0 0.3rem
            height 0.3rem
            line-height 0.3rem
            color #fff
            font-size 0.16rem
            background transparent
            border 0.01rem solid #fff
            display inline-block
            vertical-align initial

          a
            color #fff
            font-size 0.18rem
            padding 0 0.2rem
            display inline-block
            text-decoration none

        .tableScroll
          overflow hidden
          position relative
          max-height 4.4rem

        .tableWarpper
          overflow hidden
          border-radius 0 0.1rem 0.1rem 0.1rem
          box-shadow 0 0 0.5rem #0e95d2 inset

          .placeholder
            width 100%
            color #fff
            line-height 200px
            text-align center

          .table_tr
            background #0a2341

            &:nth-child(even)
              background #0a1631

          .row
            list-style none
            display flex

            li
              float left
              text-align center
              line-height 0.4rem
              display flex
              justify-content center
              align-items center
              color #fff
              font-size 0.16rem
              border-left 0.01rem solid rgba(255, 255, 255, 0.2)

          .table_title
            height 0.68rem
            line-height 0.68rem
            background #0a1631

            li
              color #2ddfff
              font-size 0.18rem

    .content
      width 100%
      height 100%
      display flex
      flex-direction column
      padding .2rem .3rem
      box-sizing border-box

      .c-top
        flex 1
        display flex

        .t-left
          flex 2
          height 100%
          position relative

          .detail
            height calc(100% - 0.28rem)
            display flex
            flex-direction column
            justify-content center
            align-items center

            .all-count
              height 0.48rem
              padding 0.2rem 0
              >>>.inner
                margin-left -70px
        .t-center
          flex 3
          height 100%
          position relative

          .inner
            width 100%
            height calc(100% - 0.28rem)

        .t-right
          flex 2
          height 100%
          .charts
            height calc(100% - 0.28rem)
      .c-bottom
        flex 1
        background url('../../../assets/c-bottom-bg1.png') no-repeat center
        background-size 100% 100%
        position relative
        display flex
        box-sizing border-box
        padding .7rem .3rem 0 .3rem
        margin 0 .2rem .1rem .2rem

        .menu
          position absolute
          top 10px
          left 160px
          width 120px
          height 35px
          z-index 1

          .curitem
            width 120px
            line-height 35px
            font-size 20px
            color #fff
            font-weight 700
            text-align center
            cursor pointer

          ul.menu-list
            position absolute
            top -30px
            left 125px
            // width 230px
            width 460px
            padding 15px
            box-sizing border-box
            background-color rgba(9, 39, 109, 0.8)
            .sj-l
              width 10px
              height 5px
              background #3a9bfa
              display block
              position absolute
              top -5px
              left 0
            .sj-r
              width 10px
              height 5px
              background #3a9bfa
              display block
              position absolute
              top -5px
              right 0
            .sj-t
              width 10px
              height 5px
              background #3a9bfa
              display block
              position absolute
              bottom -5px
              left 0
            .sj-b
              width 10px
              height 5px
              background #3a9bfa
              display block
              position absolute
              bottom -5px
              right 0
            li.item
              float left
              // width 50%
              width 25%
              font-size 18px
              color #fff
              line-height 31px
              text-align center
              box-sizing border-box
              margin 5px 0
              padding 0 12px
              cursor pointer
              &:nth-child(odd)
                // border-right 1px dashed #91e3f7

              &.active
                font-weight 700
                color #000
                border-radius 30px
                background-image -webkit-linear-gradient( 0deg, rgb(255,170,49) 0%, rgb(65,192,251) 100%)

        
        .b-left
          flex 1.7
          height 100%
          .charts
            width 100%
            height 90%
        .b-center
          flex 3.3
          height 100%
          display flex
          .w-charts
            flex 1
            height 100%
            .charts
              width 100%
              height 90%
        .b-right
          flex 2
          height 100%
          .charts
            width 100%
            height 90%


</style>
